import type { FunctionComponent, ReactElement } from 'react';
import React from 'react';

import { styles } from './FileDropzone.styles';
import { useFileDropzone } from './hooks/useFileDropzone';

interface FileDropzoneProps {
  children: ReactElement;
  location: string;
  onDrop: (files: File[]) => void;
  noClick?: boolean;
  noDrag?: boolean;
  noKeyboard?: boolean;
  enabled?: boolean;
}

export const FileDropzone: FunctionComponent<FileDropzoneProps> = ({
  children,
  location,
  onDrop,
  noClick,
  noDrag,
  noKeyboard,
  enabled = true,
}): ReactElement => {
  const { getRootProps, getInputProps, isDragActive } = useFileDropzone({ noClick, noDrag, noKeyboard, onDrop });

  if (enabled) {
    return (
      <span {...getRootProps()} data-testid={`dropzone-${location}`}>
        <input {...getInputProps()} data-testid={`dropzone-${location}-input`} />
        {children}
        {isDragActive && <div css={styles.dropzone} />}
      </span>
    );
  }
  return children;
};
