import { useDropzone } from 'react-dropzone';

export interface UseDropzoneProps {
  noClick?: boolean;
  noDrag?: boolean;
  noKeyboard?: boolean;
  onDrop: (files: File[]) => void;
}

export const useFileDropzone = ({ noClick, noDrag, noKeyboard, onDrop }: UseDropzoneProps) => {
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ noClick, noDrag, noKeyboard, onDrop });

  return {
    getRootProps,
    getInputProps,
    isDragActive,
  };
};
